// example

.example {
  background-color: #eaeaea;
}

.example__wrapper {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0 20px;
  padding-top: 25px;
}

.example__title {
  margin: 0;
  margin-bottom: 40px;
  font-size: 36px;
  line-height: 40px;
}

.example__text {
  margin: 0;
  margin-bottom: 20px;
}

.example__list {
  display: flex;
  justify-content: space-between;
  align-content: center;
  flex-wrap: wrap;
  margin: 0;
  margin-bottom: 20px;
}

.example__number {
  margin-bottom: 7px;
  font-size: 24px;
  line-height: 24px;
}

.example__description {
  margin-bottom: 20px;
  font-size: 12px;
  line-height: 12px;
}

.example__result {
  margin: 0;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}

.slider-comparison__image img {
  display: block;
  margin: 0 auto;
}

.range-wrapper__label {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.range-wrapper__text {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

@media (min-width: @tablet-width) {
  .example__wrapper {
    width: 708px;
    padding: 0 30px;
    padding-top: 43px;
  }

  .example__title {
    margin-bottom: 63px;
    font-size: 60px;
    line-height: 60px;
  }

  .example__text {
    margin-bottom: 54px;
    font-size: 16px;
    line-height: 24px;
  }

  .example__description {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 14px;
  }

  .example__stats {
    display: flex;
    justify-content: space-between;
  }

  .example__list {
    justify-content: start;
  }

  .example__item {
    width: 162px;
    margin-right: 42px;
  }

  .example__number {
    margin-bottom: 11px;
    font-size: 30px;
    line-height: 37px;
  }

  .example__result {
    margin: 0;
    font-size: 20px;
    line-height: 20px;
  }
}

@media (min-width: @desktop-width) {
  .example {
    display: flex;
    justify-content: space-between;
    background-color: @basic_grey;
  }

  .example__wrapper {
    width: 436px;
    margin: 0;
  }

  .example__title {
    margin-bottom: 146px;
  }

  .example__text {
    margin-bottom: 69px;
    font-size: 16px;
    line-height: 24px;
  }

  .example__item {
    width: 162px;
    min-height: 77px;
  }
}
